
import { defineComponent } from "vue";
import {
  specialCharList,
  SpecialCharType,
  specialCharStruct,
} from "@/Data/SpecialChars";

export default defineComponent({
  name: "HeaderTemplate",
  data() {
    return {
      items: specialCharList as specialCharStruct[],
    };
  },
  methods: {
    copyToClipboard: (content: string) => {
      navigator.clipboard.writeText(content);
    },
    doCopy: function (event: Event) {
      // è‡ªåˆ†è‡ªèº«ã‚’å–å¾—
      const targetElement = event.target as HTMLButtonElement;
      // ã‚¨ãƒ©ãƒ¼ãƒˆãƒ©ãƒƒãƒ—
      const textarea =
        targetElement.previousElementSibling as HTMLTextAreaElement;
      textarea.select();
      const content = textarea.value;
      if (content == null) return;
      // ã‚³ãƒ”ãƒ¼ã•ã‚Œã¾ã—ãŸï¼ã¨ã„ã†ãƒ¡ãƒƒã‚»ãƒ¼ã‚¸ã‚’è¡¨ç¤ºã™ã‚‹
      this.copyToClipboard(content);
      targetElement.classList.add("copied");
      targetElement.textContent = "copied!";
      setTimeout(() => {
        targetElement.classList.remove("copied");
        targetElement.textContent = "copyâ™ª";
        window.getSelection()?.removeAllRanges();
      }, 700);
    },
  },
});
